html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "SuisseIntl", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  background-color: #fdfaf5;
}

img {
  max-width: 100%;
}

.is-outer {
  position: relative !important; // stylelint-disable-line declaration-no-important
  left: -9999px !important; // stylelint-disable-line declaration-no-important
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.for-mobile {
  display: none;

  @include mb {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important; // stylelint-disable-line declaration-no-important
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  box-sizing: border-box;
  margin: 0 auto;
  min-width: 320px;
  max-width: 1740px;
  padding: 0 40px;
  &--narrow {
    padding-left: 250px;
    @include xl {
      padding-left: 230px;
    }
  }

  @include sm {
    padding: 0 14px;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.visually-hidden {
  position: absolute;
  z-index: -1;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip: rect(0 0 0 0);
}

.btn {
  display: inline-block;
  text-align: center;
  position: relative;
  background-color: #555555;
  border-radius: 24px;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  white-space: nowrap;
  text-transform: uppercase;
  color: #efefef;
  box-sizing: border-box;
  text-decoration: none;
  transition: opacity 0.3s;
  &:hover {
    opacity: 0.9;
  }
  &:active {
    opacity: 0.7;
  }
  &--long {
    padding: 18px 60px 18px 30px;
    &::before {
      content: "";
      position: absolute;
      right: 30px;
      top: 50%;
      background-image: url(../img/icon-arrow.svg);
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      width: 12px;
      height: 12px;
      transform: translateY(-50%) rotate(145deg);
    }
    &-high {
      padding: 33px 55px;
      border-radius: 60px;
      padding-right: 70px;
    }
  }
  &--short {
    padding: 16px 22px;
    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      background-image: url(../img/icon-arrow.svg);
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      width: 12px;
      height: 12px;
      transform: translate(-50%, -50%) rotate(180deg);
    }
  }
  &--outline {
    background-color: transparent;
    color: $black;
    border: 1px solid $mine-shaft;
    &::before {
      background-image: url(../img/icon-arrow-black.svg);
    }
  }
}

.section-title {
  font-weight: 700;
  font-size: 36px;
  line-height: 42px;
  text-transform: uppercase;
  color: $mine-shaft;
  @include md {
    font-size: 24px;
    line-height: 32px;
  }
  @include md {
    font-size: 18px;
    line-height: 24px;
  }
}

.input {
  border: none;
  padding: 20px 0;
  width: 100%;
  background-color: transparent;
  border-bottom: 1px solid #c4c4c4;
  &::placeholder {
    text-transform: uppercase;
    font-size: 14px;
    line-height: 1;
    @include md {
      font-size: 12px;
    }
  }
}

.btn-burger {
  box-sizing: border-box;
  width: 33px;
  height: 10px;
  position: relative;
  border-top: 2px solid $mine-shaft;
  border-bottom: 2px solid $mine-shaft;
  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: $mine-shaft;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
  }
}

.slider-button {
  position: absolute;
  background-color: #555555;
  box-sizing: border-box;
  padding: 40px;
  border-radius: 100%;
  top: 36%;
  @include lg {
    padding: 30px;
  }
  @include mb {
    padding: 20px;
  }
  &::before {
    content: "";
    position: absolute;
    background-image: url(../img/icon-arrow.svg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    width: 24px;
    height: 24px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    @include mb {
      width: 16px;
      height: 16px;
    }
  }
  &--next {
    &::before {
      transform: translate(-50%, -50%) rotate(180deg);
    }
  }
}

.quantity {
  position: relative;
  display: inline-block;
  border: 5px solid transparent;
  box-sizing: border-box;
  font-size: 12px;
  line-height: 1;
  color: #2d2d2d;
  padding: 8px;
  border-radius: 100%;
  &::before {
    content: "";
    left: 0;
    top: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    padding: 8px;
    border: 1px solid #c5c2bb;
    border-radius: 100%;
    font-size: 12px;
  }
}
